/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

.recommendations-container {
    padding: 40px 20px;
    background-color: #f9f9f9;
    text-align: center;
    opacity: 0;
    transform: translateY(50px);
    transition: opacity 1.5s ease-out, transform 1.5s ease-out;
}

.animate-recommendations {
    opacity: 1;
    transform: translateY(0);
}

.recommendations-title {
font-family: 'Gabriola', sans-serif !important;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: scale(0.8) rotateX(15deg); /* Start with a 3D rotation */
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.animate-recommendations .recommendations-title {
    opacity: 1;
    transform: scale(1) rotateX(0); /* Return to normal size and angle */
}

.recommendations-title .highlight {
    color: #f54b64;
}

.recommendations-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    transition: opacity 1s ease-out 0.7s;
}

.animate-recommendations .recommendations-title::after {
    opacity: 1;
}

.recommendations-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
    opacity: 0;
    transform: scale(0.8) translateY(30px);
    transition: opacity 1.2s ease-out 0.7s, transform 1.2s ease-out 0.7s;
}

.animate-recommendations .recommendations-grid {
    opacity: 1;
    transform: scale(1) translateY(0);
}

.recommendation-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 300px;
    text-align: left;
    opacity: 0;
    transform: translateX(-100px) scale(0.9); /* Start with an offset and smaller size */
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.recommendation-card:nth-child(odd) {
    transition-delay: 0.7s; /* Staggered delay for odd items */
}

.recommendation-card:nth-child(even) {
    transition-delay: 0.9s; /* Staggered delay for even items */
}

.animate-recommendations .recommendation-card {
    opacity: 1;
    transform: translateX(0) scale(1); /* Return to original position and size */
}

.recommendation-card:hover {
    transform: translateY(-10px) scale(1.05); /* Slight hover effect */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.recommender-name,
.recommender-position,
.recommendation-text {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.animate-recommendations .recommender-name,
.animate-recommendations .recommender-position,
.animate-recommendations .recommendation-text {
    opacity: 1;
    transform: translateY(0);
}

.recommender-name {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
}

.recommender-position {
    font-size: 1rem;
    font-weight: bold;
    color: #666;
    margin-bottom: 10px;
}

.recommendation-text {
    font-size: 1rem;
    color: #444;
    font-style: italic;
    text-align: justify;
    margin-top: 10px;
    white-space: normal;
    line-height: 1.5;
}

@media (max-width: 768px) {
    .recommendations-container {
        padding: 30px 10px;
    }

    .recommendations-title {
        font-size: 2rem;
    }

    .recommendation-card {
        width: 100%;
        transform: translateX(-50px) scale(0.9);
    }

    .animate-recommendations .recommendation-card {
        transform: translateX(0) scale(1);
    }
}

@media (max-width: 480px) {
    .recommendations-title {
        font-size: 1.8rem;
    }

    .recommendation-card {
        transform: translateX(-30px) scale(0.95);
    }

    .animate-recommendations .recommendation-card {
        transform: translateX(0) scale(1);
    }
}
