/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

.awards-container {
    padding: 40px 20px;
    background-color: #eef7ff;
    text-align: center;
    opacity: 0;
    transform: translateY(100px);
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.animate-awards {
    opacity: 1;
    transform: translateY(0);
}

.awards-title {
font-family: 'Gabriola', sans-serif !important;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: scale(0.8) rotateX(20deg); /* Start with a 3D rotation */
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.animate-awards .awards-title {
    opacity: 1;
    transform: scale(1) rotateX(0); /* Return to normal size and angle */
}

.awards-title .highlight {
    color: #f54b64;
}

.awards-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    transition: opacity 1s ease-out 1.2s;
}

.animate-awards .awards-title::after {
    opacity: 1;
}

.awards-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
    perspective: 1000px; /* Adds depth for 3D rotation */
}

.award-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 300px;
    text-align: center;
    opacity: 0;
    transform: rotateY(-45deg) translateX(-50px) scale(0.8); /* Start with a rotation, translation, and smaller size */
    transform-origin: left; /* Rotate from the left edge */
    transition: opacity 1.2s ease-out, transform 1.2s ease-out;
    transition-delay: 0.5s;
}

.award-card:nth-child(odd) {
    transition-delay: 0.7s;
}

.award-card:nth-child(even) {
    transition-delay: 1s;
}

.animate-awards .award-card {
    opacity: 1;
    transform: rotateY(0) translateX(0) scale(1); /* Return to normal size and position */
}

.award-card:hover {
    transform: translateY(-5px) scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.award-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    margin-bottom: 15px;
}

.award-title {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
}

.award-description {
    font-size: 1rem;
    color: #666;
    margin-bottom: 5px;
}

.award-year {
    font-size: 0.9rem;
    color: #999;
}

@media (max-width: 768px) {
    .awards-container {
        padding: 30px 10px;
    }

    .awards-title {
        font-size: 2rem;
    }

    .award-card {
        width: 100%;
        transform: translateX(-30px) scale(0.9); /* Slightly smaller and offset */
    }

    .animate-awards .award-card {
        transform: translateX(0) scale(1);
    }
}

@media (max-width: 480px) {
    .awards-title {
        font-size: 1.8rem;
    }

    .award-card {
        transform: rotateY(-30deg) translateX(-20px) scale(0.95); /* Less dramatic on small screens */
    }

    .animate-awards .award-card {
        transform: rotateY(0) translateX(0) scale(1);
    }
}
