/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

.my-skills-container {
    font-family: 'Gabriola', sans-serif !important;
    padding: 40px 20px;
    background-color: #eef7ff;
    text-align: center;
    opacity: 0; /* Start hidden */
    transform: translateY(20px); /* Start slightly shifted down */
    transition: opacity 1s ease-out, transform 1s ease-out; /* Slower transition */
}

/* Add this class when the Skills section enters the viewport */
.animate-skills {
    opacity: 1;
    transform: translateY(0);
}

.skills-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    position: relative;
    display: inline-block;
    opacity: 0;
    transition: opacity 1.5s ease-out 0.5s; /* Slower transition */
}

.animate-skills .skills-title {
    opacity: 1;
}

.skills-title .highlight {
    color: #f54b64;
}

.skills-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    transition: opacity 1.5s ease-out 0.7s; /* Slower transition */
}

.animate-skills .skills-title::after {
    opacity: 1;
}

.skills-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
}

.skill-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 150px;
    transition: transform 0.5s ease, box-shadow 0.5s ease; /* Slower transition */
    opacity: 0;
    transform: translateY(20px); /* Start slightly shifted down */
    transition: opacity 1s ease-out, transform 1s ease-out; /* Slower transition */
    transition-delay: 0.7s; /* Increased delay */
}
.airflow-icon {
  filter: brightness(0) saturate(100%) invert(42%) sepia(75%) saturate(543%) hue-rotate(-417deg) brightness(103%) contrast(96%);
}

.skill-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.skill-icon {
    font-size: 3rem;
    margin-bottom: 10px;
    color: #f54b64;
}

.skill-name {
    font-size: 1.2rem;
    color: #333;
}

/* Staggered Animation for Skill Cards */
.skills-grid .skill-card:nth-child(odd) {
    transition-delay: 1s; /* Increased delay */
}

.skills-grid .skill-card:nth-child(even) {
    transition-delay: 1.3s; /* Increased delay */
}

.animate-skills .skill-card {
    opacity: 1;
    transform: translateY(0);
}

/* Responsive Design */

/* For small devices (phones, less than 600px) */
@media (max-width: 600px) {
    .skills-title {
        font-size: 2rem; /* Smaller font size for smaller screens */
    }

    .skill-card {
        width: 120px; /* Smaller cards */
        padding: 15px;
    }

    .skill-icon {
        font-size: 2.5rem; /* Smaller icons */
    }

    .skill-name {
        font-size: 1rem; /* Smaller text */
    }
}

/* For medium devices (tablets, less than 768px) */
@media (max-width: 768px) {
    .skills-title {
        font-size: 2.2rem; /* Slightly smaller font size for tablets */
    }

    .skill-card {
        width: 130px; /* Slightly smaller cards */
    }

    .skill-icon {
        font-size: 2.7rem; /* Slightly smaller icons */
    }

    .skill-name {
        font-size: 1.1rem; /* Slightly smaller text */
    }
}

/* For large devices (desktops, less than 1024px) */
@media (max-width: 1024px) {
    .skills-title {
        font-size: 2.3rem; /* Slightly smaller font size for desktops */
    }

    .skill-card {
        width: 140px; /* Slightly smaller cards */
    }

    .skill-icon {
        font-size: 2.8rem; /* Slightly smaller icons */
    }

    .skill-name {
        font-size: 1.15rem; /* Slightly smaller text */
    }
}
