/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #fff; /* Set the background color to white */
}

.resume-container {
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    max-width: 900px; /* Limit the width to a maximum */
    margin: 50px auto; /* Center the container with auto margin */
    opacity: 0; /* Start hidden */
    transform: translateY(50px); /* Start shifted down */
    transition: opacity 1.5s ease-out, transform 1.5s ease-out;
}

/* Add this class when the Resume section enters the viewport */
.animate-resume {
    opacity: 1;
    transform: translateY(0);
}

.resume-title {
font-family: 'Gabriola', sans-serif !important;
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
    font-weight: bold;
    opacity: 0;
    transform: scale(0.8); /* Start with a smaller scale */
    transition: opacity 1s ease-out 0.3s, transform 1s ease-out 0.3s;
}

.animate-resume .resume-title {
    opacity: 1;
    transform: scale(1); /* Grow to normal size */
}

.resume-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    transition: opacity 1s ease-out 0.7s;
}

.animate-resume .resume-title::after {
    opacity: 1;
}

.highlight {
    color: #f54b64;
}

.resume-content {
    display: flex;
    justify-content: space-between;
    position: relative;
    flex-wrap: wrap; /* Allow wrapping on smaller screens */
}

.resume-section {
    width: 45%;
    position: relative;
    margin-bottom: 20px; /* Add space between rows */
}

@media (max-width: 768px) {
    .resume-section {
        width: 100%; /* Full width on smaller screens */
    }

    .resume-item {
        padding: 20px;
        transform: translateY(50px) rotate(-5deg); /* Less rotation for small screens */
    }

    .icon-container {
        left: 10px; /* Adjust icon position for smaller screens */
    }

    .icon {
        font-size: 20px; /* Smaller icons */
        padding: 10px; /* Adjust padding */
    }

    .vertical-line {
        display: none; /* Hide vertical line on smaller screens */
    }
}

.resume-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    border: 2px solid #f54b64;
    border-radius: 8px;
    padding: 20px;
    background-color: white;
    position: relative;
    opacity: 0;
    transform: translateY(50px) rotate(-10deg); /* Start shifted down and slightly rotated */
    transition: opacity 1s ease-out, transform 1s ease-out;
    transition-delay: 0.5s;
}

.resume-item:nth-child(odd) {
    transition-delay: 0.7s; /* Staggered delay */
}

.resume-item:nth-child(even) {
    transition-delay: 1s; /* Staggered delay */
}

.animate-resume .resume-item {
    opacity: 1;
    transform: translateY(0) rotate(0deg); /* Move to original position and rotation */
}

.icon-container {
    position: absolute;
    left: -70px;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
}

.icon {
    font-size: 24px;
    background-color: #f54b64;
    color: white;
    padding: 14px;
    border-radius: 50%;
    z-index: 2;
    position: relative;
    opacity: 0;
    transform: scale(0.5) rotate(180deg); /* Start small and rotated */
    transition: opacity 1.2s ease-out 0.7s, transform 1.2s ease-out 0.7s;
}

.animate-resume .icon {
    opacity: 1;
    transform: scale(1) rotate(0); /* Grow to normal size and unrotate */
}

.vertical-line {
    position: absolute;
    width: 2px;
    background-color: #f54b64;
    top: 0;
    bottom: 0;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
}

.resume-section.left .vertical-line {
    left: 50%;
    height: 200px;
    width: 5px;
}

.resume-section.right .vertical-line {
    left: 50%;
    height: 200px;
    width: 5px;
}

.item-content h3 {
    margin: 0;
    font-size: 1.2rem;
}

.item-year {
    color: #f54b64;
    font-size: 0.9rem;
    margin-bottom: 5px;
    font-weight: bold;
}

@media (max-width: 600px) {
    .resume-title {
        font-size: 1.8rem; /* Adjust title size for small screens */
    }

    .resume-item {
        transform: translateY(30px) rotate(-5deg); /* Less dramatic rotation */
    }

    .icon {
        font-size: 18px; /* Smaller icons for small screens */
        padding: 8px;
    }
}
