/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

.certifications-container {
    padding: 40px 20px;
    background-color: #eef7ff;
    text-align: center;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 1s ease-out, transform 1s ease-out;
}

/* Add this class when the Certifications section enters the viewport */
.animate-certifications {
    opacity: 1;
    transform: translateY(0);
}

.certifications-title {
font-family: 'Gabriola', sans-serif !important;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    position: relative;
    display: inline-block;
    opacity: 0;
    transition: opacity 1s ease-out 0.5s;
}

.animate-certifications .certifications-title {
    opacity: 1;
}

.certifications-title .highlight {
    color: #f54b64;
}

.certifications-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    transition: opacity 1s ease-out 0.5s;
}

.animate-certifications .certifications-title::after {
    opacity: 1;
}

.certifications-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    margin-top: 30px;
}

.certification-card {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    width: 350px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
    transition-delay: 0.3s;
    will-change: transform, opacity; /* Improves animation performance */
}

.animate-certifications .certification-card {
    opacity: 1;
    transform: translateY(0);
}

.certification-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.certification-badge {
    width: 80px;
    height: 80px;
    margin-right: 20px;
    border-radius: 8px;
    opacity: 0;
    transition: opacity 0.9s ease-out;
    will-change: opacity; /* Improves performance */
}

.animate-certifications .certification-badge {
    opacity: 1;
}

.certification-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    transition: opacity 1s ease-out 0.6s;
}

.animate-certifications .certification-details {
    opacity: 1;
}

.certification-name {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
}

.certification-issuer {
    font-size: 1rem;
    color: #666;
    margin-bottom: 5px;
}

.certification-date {
    font-size: 0.9rem;
    color: #999;
}

/* Responsive Design */

/* For small devices (phones, less than 600px) */
@media (max-width: 600px) {
    .certifications-title {
        font-size: 2rem;
    }

    .certification-card {
        width: 100%;
        flex-direction: column;
        text-align: center;
    }

    .certification-badge {
        width: 60px;
        height: 60px;
        margin: 0 auto 15px;
    }

    .certification-name {
        font-size: 1.1rem;
    }

    .certification-issuer {
        font-size: 0.95rem;
    }

    .certification-date {
        font-size: 0.85rem;
    }
}

/* For medium devices (tablets, less than 768px) */
@media (max-width: 768px) {
    .certifications-title {
        font-size: 2.2rem;
    }

    .certification-card {
        width: 100%;
    }

    .certification-badge {
        width: 70px;
        height: 70px;
        margin-right: 15px;
    }

    .certification-name {
        font-size: 1.15rem;
    }

    .certification-issuer {
        font-size: 1rem;
    }

    .certification-date {
        font-size: 0.9rem;
    }
}

/* For large devices (desktops, less than 1024px) */
@media (max-width: 1024px) {
    .certifications-title {
        font-size: 2.3rem;
    }

    .certification-card {
        width: 320px;
    }

    .certification-badge {
        width: 75px;
        height: 75px;
    }

    .certification-name {
        font-size: 1.15rem;
    }

    .certification-issuer {
        font-size: 1rem;
    }

    .certification-date {
        font-size: 0.9rem;
    }
}
