/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

/* Initial state: everything hidden */
.profile-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  background-color: #f9f9f9;
  text-align: center;
  min-height: 60vh;
  margin-top: 0;
  padding-top: 0;
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.5s ease-out, transform 0.5s ease-out;
  will-change: opacity, transform; /* Hint to browser for optimization */
}

/* Add this class when the About section enters the viewport */
.animate-about {
  opacity: 1;
  transform: translateY(0);
}

.profile-image img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 15px;
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 1s ease-out 0.5s, transform 1s ease-out 0.5s;
  will-change: opacity, transform; /* Use hardware acceleration */
}

.animate-about .profile-image img {
  opacity: 1;
  transform: scale(1);
}

.profile-details {
  max-width: 800px;
  margin: 0 auto;
  padding-right: 20px;
  opacity: 0;
  transition: opacity 1s ease-out 1s;
  will-change: opacity;
}

.animate-about .profile-details {
  opacity: 1;
}

.profile-title {
font-family: 'Gabriola', sans-serif !important;
  font-size: 2.5rem;
  margin-bottom: 15px;
  color: #333;
  position: relative;
  opacity: 0;
  transition: opacity 1.5s ease-out 1.2s;
}

.animate-about .profile-title {
  opacity: 1;
}

.profile-title .highlight::after {
  content: '';
  display: block;
  width: 60px;
  height: 3px;
  background-color: #f54b64;
  position: absolute;
  left: 30%;
  transform: translateX(-50%);
  bottom: -5px;
  opacity: 0;
  transition: opacity 1.5s ease-out 1.4s;
}

.animate-about .profile-title .highlight::after {
  opacity: 1;
}

.profile-details p {
  font-size: 1rem;
  line-height: 1.6;
  color: #666;
  margin-bottom: 20px;
  text-align: justify;
  opacity: 0;
  transition: opacity 1.5s ease-out 1.6s;
}

.animate-about .profile-details p {
  opacity: 1;
}

.profile-info {
  list-style-type: none;
  padding: 0;
  margin: 20px 0;
  text-align: left;
  opacity: 0;
  transition: opacity 1.5s ease-out 1.8s;
}

.animate-about .profile-info {
  opacity: 1;
}

.download-cv {
  padding: 10px 20px;
  background-color: #f54b64;
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.3s ease;
  opacity: 0;
  transition: opacity 1.5s ease-out 2s;
}

.animate-about .download-cv {
  opacity: 1;
}

.download-cv:hover {
  background-color: #e84347;
}

@media (min-width: 768px) {
  .profile-section {
    flex-direction: row;
    text-align: left;
  }

  .profile-image {
    flex: 1;
    text-align: center;
    padding-left: 100px;
  }

  .profile-details {
    flex: 2;
    padding-left: 10px;
    padding-right: 100px;
  }
}

@media (max-width: 768px) {
  .profile-details {
    padding-left: 0;
    padding-right: 20px;
  }

  /* Keep the same animation but reduce delays to avoid lag on mobile */
  .profile-section {
    transition: opacity 0.5s ease-out, transform 0.5s ease-out;
  }

  .profile-image img {
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
  }

  .profile-details {
    transition: opacity 0.8s ease-out;
  }

  .profile-title {
    transition: opacity 1.2s ease-out;
  }

  .profile-title .highlight::after {
    transition: opacity 1.2s ease-out;
  }

  .profile-details p,
  .profile-info,
  .download-cv {
    transition: opacity 1s ease-out;
  }
}
