/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

/* Home Section Styles */
.home {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 100px;
  min-height: 100vh;
  background: radial-gradient(circle, #1a1a1a, #2e2e2e);
  overflow: hidden;
  margin-bottom: 0; /* Ensure no bottom margin */
}

/* Neural Network Animation */
.neural-network-animation {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  opacity: 0.5;
}

/* Font Styling for Main Text */
.main-heading,
.sub-heading,
.highlighted-text {
  font-family: 'Gabriola', sans-serif;
}

@keyframes moveParticles {
  0% { background-position: 0 0; }
  100% { background-position: 100% 100%; }
}

/* Text Animation */
.text-reveal .highlighted-text {
  animation: fadeInLeft 2s cubic-bezier(0.19, 1, 0.22, 1) forwards;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.text-reveal .main-heading,
.text-reveal .sub-heading,
.text-reveal .description {
  opacity: 1;
  transform: translateY(0);
}

.text-reveal .main-heading {
  animation: fadeInUp 1s ease-out forwards;
}

.text-reveal .sub-heading {
  animation: fadeInUp 1s ease-out forwards 0.5s;
}

.text-reveal .description {
  animation: fadeInUp 1s ease-out forwards 1s;
}

.main-heading {
  font-size: 5rem;
  color: white;
  opacity: 0;
  transform: translateY(50px);
}

.sub-heading {
  font-size: 2rem;
  color: #ff4a57;
  opacity: 0;
  transform: translateY(50px);
}

.highlighted-text {
  display: inline-block;
  opacity: 0;
}

.description {
  font-size: 1.5rem;
  color: #aaa;
  opacity: 0;
  transform: translateY(50px);
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .home {
    flex-direction: column;
    padding-left: 50px;
    padding-right: 50px;
    text-align: center;
  }

  .main-heading {
    font-size: 3rem;
  }

  .sub-heading {
    font-size: 2rem;
  }

  .highlighted-text {
    font-size: 2rem;
  }

  .description {
    font-size: 1.2rem;
  }
}

/* Match page background with home section */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #1a1a1a; /* Set the body background color to match the home section */
}
