.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 1000;
}

.scroll-button {
  width: 50px;
  height: 50px;
  background-color: #ff4a57;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.scroll-button i {
  color: white;
  font-size: 24px;
}

.scroll-button:hover {
  background-color: #e84347;
  transform: translateY(-5px);
}
