/* Import a clean, modern, professional font */
@import url('https://fonts.googleapis.com/css2?family=Gabriola:wght@400;600;700&display=swap');

.contact-us-container {
font-family: 'Gabriola', sans-serif !important;
    padding: 40px 20px;
    background-color: #eef7ff;
    text-align: center;
    opacity: 0;
    transform: scale(0.8) rotateX(20deg) translateY(50px);
    transition: opacity 1s ease-out, transform 1s ease-out;
}

.animate-contact {
    opacity: 1;
    transform: scale(1) rotateX(0) translateY(0);
}

.contact-title {
font-family: 'Gabriola', sans-serif !important;
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
    position: relative;
    display: inline-block;
    opacity: 0;
    transform: translateY(-50px) scale(0.8) rotateZ(-15deg);
    animation: titleAppear 1s ease-out forwards;
}

@keyframes titleAppear {
    0% {
        opacity: 0;
        transform: translateY(-50px) scale(0.8) rotateZ(-15deg);
    }
    100% {
        opacity: 1;
        transform: translateY(0) scale(1) rotateZ(0);
    }
}

.contact-title .highlight {
    color: #f54b64;
}

.contact-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 3px;
    background-color: #f54b64;
    margin: 8px auto 0;
    opacity: 0;
    animation: lineAppear 1.2s ease-out forwards 0.5s;
}

@keyframes lineAppear {
    0% {
        width: 0;
        opacity: 0;
    }
    100% {
        width: 60px;
        opacity: 1;
    }
}

.contact-links {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
    opacity: 0;
    transform: translateY(50px) scale(0.9);
    transition: opacity 1s ease-out, transform 1s ease-out;
    transition-delay: 1s;
}

.animate-contact .contact-links {
    opacity: 1;
    transform: translateY(0) scale(1);
}

.contact-link {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
    color: #333;
    text-decoration: none;
    transform: rotateY(-90deg) scale(0.5); /* Start rotated and smaller */
    opacity: 0;
    transition: transform 0.5s ease-out, opacity 0.5s ease-out;
}

.contact-link:nth-child(1) {
    transition-delay: 1.2s;
}

.contact-link:nth-child(2) {
    transition-delay: 1.4s;
}

.contact-link:nth-child(3) {
    transition-delay: 1.6s;
}

.animate-contact .contact-link {
    transform: rotateY(0) scale(1); /* Return to normal */
    opacity: 1;
}

.contact-link:hover {
    color: #f54b64;
    transform: scale(1.1); /* Slight grow on hover */
}

.contact-link i {
    margin-right: 10px;
    font-size: 2rem;
}

@media (max-width: 768px) {
    .contact-title {
        font-size: 2rem;
    }

    .contact-link {
        font-size: 1.2rem;
    }
}

@media (max-width: 480px) {
    .contact-title {
        font-size: 1.8rem;
    }

    .contact-link {
        font-size: 1rem;
    }
}
